.extensions {
  margin-bottom: 210px;

  &__cards {
    display: flex;
    justify-content: center;
    gap: 33px;

    @include tablet800 {
      flex-direction: column;
      align-items: center;
    }
  }

  &__card {}
}

.card {
  max-width: 476px;
  width: 100%;
  padding: 40px 35px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 30px;

  color: var(--black-text);
  background: var(--light-text);
  border-radius: 10px;
  box-shadow: 0px 4px 31px 0px rgba(0, 0, 0, 0.15);

  &__content {
    max-width: 375px;
    text-align: center;
  }

  &__title {
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    letter-spacing: 0.01em;
    text-align: center;
    color: var(--black-text);
  }

  &__description {
    letter-spacing: 0.01em;
    text-align: center;
    color: var(--second-text);
  }

  &__img {}
}