.footer {
  padding-bottom: 107px;

  &__inner {
    margin: 0 auto;
    max-width: 930px;
    display: grid;
    grid-template-columns: repeat(3, 121px) auto;
    gap: 75px;

    @include tablet800 {
      grid-template-columns: repeat(3, 1fr);
    }

    @include mobile {
      max-width: 220px;
      grid-template-columns: 1fr;
    }
  }

  &__menu-title {
    margin-bottom: 40px;
    font-size: 20px;
  }

  &__menu-item:not(:last-child) {
    margin-bottom: 19px;
  }

  &__menu-link {
    &:focus {
      outline: none;
    }

    &:focus-visible {
      outline: 2px dashed var(--primary);
      outline-offset: 3px;
    }

    @media (any-hover:hover) {
      &:hover {
        color: var(--secondary-one);
      }
    }

    &:active {
      color: var(--secondary-one);
    }
  }

  &__contacts {
    margin-left: auto;

    @include tablet800 {
      grid-column: 1 / -1;
      margin: 0;
    }
  }

  &__address {
    margin-bottom: 35px;
  }

  &__contacts-street {
    padding-left: 54px;
    margin-bottom: 25px;
    display: block;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 46px;
      height: 46px;
      background-image: url('./../img/map.svg');
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  &__contacts-phone {
    padding-left: 45px;
    display: block;
    position: relative;

    &:hover {
      color: var(--secondary-one);
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 35px;
      height: 35px;
      background-image: url('./../img/phone.svg');
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
}

.social-media {
  display: flex;
  gap: 24px;

  &__icon {
    width: 42px;
    height: 42px;
    fill: var(--light-text);
    transition: fill .3s ease;

    &:hover {
      fill: var(--secondary-one);
    }
  }
}