.partners {
  margin-bottom: 210px;

  &__list {
    margin: 0 auto;
    margin-bottom: 50px;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    flex-wrap: wrap;
    justify-items: center;
    gap: 50px;

    @include tablet800 {
      grid-template-columns: repeat(3, 1fr);
    }

    @include mobile425 {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__img {
    max-width: none;
  }
}