.possibilities {
  margin-bottom: 220px;

  &__title {
    margin-bottom: 27px;
    font-weight: 400;
    font-size: 52px;
    line-height: 1.19;
    letter-spacing: 0em;
    color: var(--light-text);

    @include mobile425 {
      font-size: 40px;
    }
  }



  &__inner {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 57px 27px;

    @include tablet800 {
      flex-direction: column;
    }
  }

  &__picture {}

  &__img {}

  &__list {
    width: 307px;
    display: flex;
    flex-direction: column;
    gap: 25px;
  }

  &__info {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  &__info-wrap {
    display: flex;
    gap: 10px;
  }

  &__info-svg {
    width: 30px;
    height: 30px;
    fill: var(--secondary-one);
  }

  &__info-title {
    width: 100%;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.5;
    letter-spacing: 0.01em;
    color: var(--light-text);
  }

  &__info-desc {
    width: 100%;
  }
}