.hero {
  padding-bottom: 152px;
  margin-bottom: 162px;

  @include mobile {
    padding-bottom: 100px;
  }

  @include mobile425 {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  &__content {
    text-align: center;
  }

  &__title {
    margin-bottom: 27px;
    font-weight: 500;
    font-size: 80px;
    line-height: 1.1;
    letter-spacing: 0em;

    @include tablet800 {
      font-size: 52px;
      line-height: 1.19;
    }

    @include mobile425 {
      font-size: 33px;
    }
  }



  &__buttons {
    margin-bottom: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 21px 35px;

    @include mobile {
      margin-bottom: 57px;
      flex-direction: column;
    }
  }

  &__btn {}

  &__picture {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: -152px;
      width: 100%;
      height: 551px;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #0e0e0e 66.15%);

      @include mobile {
        bottom: -18px;
        height: 150px;
      }
    }
  }

  &__img {}
}