.header {
  padding: 33px 0;
  margin-bottom: 95px;

  @include tablet800 {
    margin-bottom: 67px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mobile425 {
      flex-direction: column;
      gap: 20px 0;
    }
  }
}

.nav {
  margin: 0 10px;

  @include tablet800 {
    display: none;
  }

  &__list {
    display: flex;
    gap: 41px;
  }

  &__item {}

  &__link {
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 0.01em;
    text-align: center;
    color: var(--light-text);
    transition: color .3s ease-in-out;

    &:focus {
      outline: none;
    }

    &:focus-visible {
      outline: 2px dashed var(--primary);
      outline-offset: 3px;
    }

    @media (any-hover:hover) {
      &:hover {
        color: var(--secondary-one);
      }
    }

    &:active {
      color: var(--secondary-one);
    }
  }
}

.btn {
  padding: 13px 46px;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 17px;
  line-height: 1.47;
  letter-spacing: 0.01em;
  color: var(--light-text);
  border: 1px solid var(--light-text);
  box-shadow: 0px 4px 31px 0px rgba(0, 0, 0, 0.15);
  // transition: color .3s ease-in-out, background-color .5s ease;

  transition: all .5s;
  position: relative;
  overflow: hidden;
  z-index: 1;

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--dark-background);
    z-index: -2;
  }

  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: var(--secondary-two);

    transition: all .3s;
    z-index: -1;
  }

  &:hover {
    color: var(--secondary-one);

    &:before {
      width: 100%;
    }
  }

  &:hover {
    // color: var(--secondary-one);
    background-color: var(--secondary-two);
  }

  &:active {
    border-color: transparent;
    transform: scale(.9);
  }
}

.btn--primary {
  background-color: var(--primary);
  border-color: var(--secondary-two);

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--primary);
    z-index: -2;
  }

  &:hover {
    background-color: var(--secondary-two);
  }

  &:active {
    // border-color: #fff;

  }
}