.reviews {
  margin-bottom: 257px;

  &__cards {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px 23px;
  }

  &__card {
    padding: 40px;
    max-width: 430px;
    border: 1px solid #dedede;
    border-radius: 10px;
  }

  &__info {
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    gap: 13px;
  }

  &__name {
    display: block;
  }

  &__prof {
    display: block;
  }
}