.gallery {
  margin-bottom: 210px;

  &__wrapper {
    margin-bottom: 44px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 44px 33px;

  }

  @include mobile {
    &__pic:nth-child(n + 5) {
      display: none;
    }
  }

  &__img {
    display: block;
    border-radius: 12px;
    transition: transform .4s ease;
    cursor: pointer;

    &:hover {
      transform: scale(1.2);
    }

    @include mobile {
      width: 325px;
      height: 285px;
    }
  }
}